import { useCallback, useRef, useState } from 'react';
import { requestInstance } from '.';
import useDepEffect from '../hooks/useDepEffect';
import useMount from '../hooks/useMount';
import { FetchOptions, FetchRequestConfig, FetchResponse, UseFetchReturnType } from './interface';

/**
 * @description: hook请求
 * @generic T 请求参数
 * @generic R 响应结构
 * @param {FetchRequestConfig} config 不管是GET还是POST请求都使用data
 * @returns {Promise}
 */
const useFetch = <T = any, R = any>(
  config: FetchRequestConfig<T, R>,
  options?: FetchOptions,
): UseFetchReturnType<T, R> => {
  // loading状态
  const [loading, setLoading] = useState<boolean>(false);
  // 接口返回的数据
  const [data, setData] = useState<R>();

  const configRef = useRef(config);
  configRef.current = config;

  // * 异步请求
  const requestFn = useCallback((data?: T | null, newConfig?: FetchRequestConfig<T, R>): Promise<FetchResponse<R>> => {
    return new Promise((resolve) => {
      setLoading(true);
      requestInstance
        ?.request<T, FetchResponse<R>>({
          ...configRef.current,
          data: data === undefined || null ? configRef.current.data : data,
          ...newConfig,
        })
        .then((res: FetchResponse<R>) => {
          setData(res.data);
          resolve(res);
          return res;
        })
        .catch((err: FetchResponse<R>) => {
          const errRes = { msg: err } as any;
          resolve(errRes);
          return errRes;
        })
        .finally(() => {
          setLoading(false);
        });
    });
  }, []);

  /**
   * 发送请求
   */
  const sendRequest = (data?: T | null, newConfig?: FetchRequestConfig<T, R>) => requestFn(data, newConfig);

  /**
   * 取消请求
   * @param url 取消的url名称
   * @returns void
   */
  const cancel = () => requestInstance?.cancelRequest(configRef.current.url);

  /**
   * 取消全部请求
   * @returns
   */
  const cancelAll = () => requestInstance?.cancelAllRequest();

  useMount(() => {
    if (!options?.manual) {
      requestFn();
    }
  });

  useDepEffect(() => {
    if (!options?.manual) {
      requestFn();
    }
  }, [configRef.current]);

  return { loading, data, sendRequest, cancel, cancelAll };
};

export default useFetch;
